const foodWidget = $ => {
	const carouselElement = $('#slider-items');

	carouselElement.slick({
		infinite: true,
		slidesToShow: 3,
		slidesToScroll: 1,
		arrows: false,
		asNavFor: '#text-items'
	});

	const textElements = $('#text-items');

	textElements.slick({
		slidesToShow: 1,
		slidesToScroll: 1,
		arrows: false,
		fade: true
	});
};

module.exports = foodWidget;



